import cn from "classnames";
import { memo, useEffect, useState } from "react";
import { FaExpandArrowsAlt } from "react-icons/fa";
import { TiZoom } from "react-icons/ti";
import { useSelector } from "react-redux";
import {
  selectCharData,
  selectCurSetupI,
  selectSetups
} from "../../app/calculatorSlice";
import { selectCmpSetupIs } from "../../app/uiSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box, MobileNavBtn } from "../../styledCpns/Layout";
import { DmgTables } from "../CommonCpns/DmgTables";
import Modal from "../CommonCpns/Modal";
import { Card, TrackerCtrlBtn, MobileTrackerPos } from "./styles";
import Tracker from "./Tracker";

export default function DmgResults() {
  const { name } = useSelector(selectCharData);
  const cmpSetupIs = useSelector(selectCmpSetupIs);
  const [enlarged, setEnlarged] = useState(false);
  const [trackerState, setTrackerState] = useState(0);

  useEffect(() => {
    const ctrlTrackerState = (e) => {
      if (e.key === "t") setTrackerState(trackerState === 1 ? 2 : 1);
    };
    window.addEventListener("keydown", ctrlTrackerState);
    return () => window.removeEventListener("keydown", ctrlTrackerState);
  }, [trackerState]);

  useEffect(() => {
    if (cmpSetupIs.length > 1) {
    }
  }, [cmpSetupIs]);

  return (
    <Card className="px-3 pt-2 pb-4 flex-col relative" bgColor="cardBg3">
      {window.innerWidth >= 610 && (
        <AbsoluteBox top="0.75rem" left="0.75rem">
          <IconBtn
            className="glow-on-hover"
            variant="positive"
            size="1.75rem"
            onClick={() => setEnlarged(true)}
          >
            <FaExpandArrowsAlt />
          </IconBtn>
        </AbsoluteBox>
      )}
      <AbsoluteBox top="0.75rem" right="0.75rem">
        <TrackerCtrlBtn
          className="glow-on-hover"
          size="1.75rem"
          state={trackerState}
          onClick={() => setTrackerState([0, 2].includes(trackerState) ? 1 : 0)}
        >
          <TiZoom size="1.25rem" />
        </TrackerCtrlBtn>
      </AbsoluteBox>
      <MemoInner name={name} />
      {enlarged && (
        <EnlargedInner name={name} close={() => setEnlarged(false)} />
      )}
      {trackerState > 0 && (
        <Tracker
          trackerState={trackerState}
          setTrackerState={setTrackerState}
        />
      )}
      {window.innerWidth < 1050 && trackerState > 0 && (
        <MobileTrackerPos>
          <MobileNavBtn
            className={cn({ showing: trackerState > 0 })}
            onClick={() => setTrackerState(1)}
          >
            <TiZoom size="1.75rem" />
          </MobileNavBtn>
        </MobileTrackerPos>
      )}
    </Card>
  );
}

function EnlargedInner({ name, close }) {
  return (
    <Modal close={close}>
      <Box
        className="px-4 pt-2 pb-3 flex-col"
        height="90%"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <Inner name={name} />
      </Box>
    </Modal>
  );
}

const MemoInner = memo(Inner);

function Inner({ name }) {
  const setups = useSelector(selectSetups);
  const cmpSetupIs = useSelector(selectCmpSetupIs);
  const curI = useSelector(selectCurSetupI);
  const allDmg = useSelector((state) => state.calculator.allDmg);
  const [focus, setFocus] = useState("Average");
  return (
    <>
      {cmpSetupIs.length > 1 ? (
        <div className="mb-3 flex justify-center">
          <Text className="mr-2">Choose a focus</Text>
          <Select
            fontSize="1rem"
            align="center"
            color="lightGold"
            value={focus}
            onChange={(e) => setFocus(e.target.value)}
          >
            {["Non-crit", "Crit", "Average"].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </Select>
        </div>
      ) : (
        <Text className="mx-3 my-2" bold align="center">
          {setups[curI].name.toUpperCase()}
        </Text>
      )}
      <div className="grow-1 hide-sb">
        <DmgTables
          resetSignal={name}
          charName={name}
          dmg={allDmg[curI]}
          focus={cmpSetupIs.length > 1 ? focus : null}
        />
      </div>
    </>
  );
}
