import { useSelector } from "react-redux";
import {
  selectMyWps,
  selFilteredWpIds,
  selWpById
} from "../../app/databaseSlice";
import { useGetHeight } from "../../helpers/hooks";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, CloseBtn } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { EquippedChar } from "./Common";
import useInvRack from "./hooks/useInvRack";
import { MyWpCard } from "./MyItemCard";
import { DbCard, Inventory, StyledHeading } from "./styles";

export default function WpInventory({
  wpType,
  owner,
  btnText,
  btnClick,
  close
}) {
  const filteredIds = useSelector((state) => selFilteredWpIds(state, [wpType]));
  const [hRef, height] = useGetHeight();
  const [invRack, chosenID] = useInvRack({
    items: useSelector(selectMyWps),
    itemType: "Weapon",
    filteredIds
  });
  const chosenWp = useSelector((state) => selWpById(state, chosenID));

  return (
    <Modal standard close={close}>
      <Box height="10%" className="p-2">
        <StyledHeading>
          {window.innerWidth > 380 && <p className="heading_text">{wpType}</p>}
          <CloseBtn className="close_btn" onClick={close} />
        </StyledHeading>
      </Box>
      <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
        <Inventory className="hide-sb">
          {invRack}
          <div ref={hRef} className="flex-col justify-between">
            <DbCard
              className="flex-col"
              style={{
                minHeight: "26rem",
                maxHeight: height / 16 - 3 + "rem"
              }}
            >
              <Box className="grow-1 hide-sb" width="17rem">
                <MyWpCard weapon={chosenWp} mutable={false} />
              </Box>
              {chosenWp && chosenWp.user !== owner ? (
                <ButtonBar
                  className="mt-3"
                  variants={["positive"]}
                  texts={[btnText]}
                  handlers={[
                    () => {
                      btnClick(chosenWp);
                      close();
                    }
                  ]}
                />
              ) : null}
            </DbCard>
            {chosenWp?.user ? <EquippedChar user={chosenWp.user} /> : null}
          </div>
        </Inventory>
      </Box>
    </Modal>
  );
}
