import cn from "classnames";
import { useEffect, useState } from "react";
import { findArtifact, findWeapon } from "../../../data/controllers";
import { NoItems } from "../Common";
import Thumb from "../Thumb";

function getWpInfo({ type, code, ...rest }) {
  const { beta, name, icon, rarity } = findWeapon({ type, code });
  return { beta, name, icon, rarity, ...rest };
}
function getArtInfo({ code, type, user, rarity, level }) {
  const { beta, name, icon } = findArtifact({ code, type });
  return { beta, name, icon, rarity, level, user };
}

export default function useInvRack({ items, itemType, filteredIds }) {
  const [chosenID, setChosenID] = useState(0);
  const [clickedID, setClickedID] = useState(null);

  useEffect(() => {
    if (!filteredIds.includes(chosenID)) {
      setChosenID(filteredIds[0] || 0);
    }
  }, [filteredIds]);

  let rackContent;
  if (!items.length) {
    rackContent = <NoItems type={itemType} />;
  } else {
    rackContent = (
      <div className="flex-wrap">
        {items.map((item) => {
          const { ID } = item;
          const getInfo = itemType === "Weapon" ? getWpInfo : getArtInfo;
          return (
            <div
              key={ID}
              className={cn("cell", { "d-none": !filteredIds.includes(ID) })}
            >
              <Thumb
                item={getInfo(item)}
                chosen={ID === chosenID}
                clicked={ID === clickedID}
                handleMouseDown={() => setClickedID(ID)}
                choose={() => {
                  setClickedID(null);
                  setChosenID(ID);
                }}
              />
            </div>
          );
        })}
      </div>
    );
  }
  return [
    <div className="rack hide-sb">{rackContent}</div>,
    chosenID,
    setChosenID
  ];
}
