import { memo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  CHANGE_TALENT,
  REFINE_WP,
  selectArt,
  selectATTRs,
  selectChar,
  selectParty,
  selectWeapon,
  TOGGLE_CONS,
  UPGRADE_WP
} from "../../../app/calculatorSlice";
import { useSwitcher } from "../../../helpers/hooks";
import { SetBonus } from "../../CommonCpns";
import ConsList from "../../Readers/ConsList";
import StatsTable from "../../Readers/StatsTable";
import { SharedSpace } from "../../Readers/styles";
import TalentList from "../../Readers/TalentList";
import WeaponCard from "../../Readers/WeaponCard";

export default memo(TabContent);

function TabContent({ tab }) {
  let content;
  if (tab === "Attributes") {
    content = <Attributes />;
  } else if (tab === "Weapon") {
    content = <Weapon />;
  } else if (tab === "Artifacts") {
    content = <Artifacts />;
  } else if (tab === "Constellation") {
    content = <Constellation />;
  } else {
    content = <Talents />;
  }
  return <div className="mt-2 pt-1 grow-1 hide-sb">{content}</div>;
}

function Attributes() {
  const ATTRs = useSelector(selectATTRs);
  return (
    <div className="full-h custom-sb">
      <StatsTable stats={ATTRs} breakdown={true} />
    </div>
  );
}

function Weapon() {
  const weapon = useSelector(selectWeapon);
  const dispatch = useDispatch();
  return (
    <div className="full-h hide-sb">
      <WeaponCard
        weapon={weapon}
        mutable={true}
        upgrade={(level) => dispatch(UPGRADE_WP(level))}
        refine={(rfm) => dispatch(REFINE_WP(rfm))}
      />
    </div>
  );
}

function Artifacts() {
  const artSBnes = useSelector((state) => {
    return state.calculator.allArtSBnes[state.calculator.curSetupI];
  });
  const { sets } = useSelector(selectArt);
  const [switcher, tab] = useSwitcher([
    { text: "Details", clickable: true },
    { text: "Set Bonus", clickable: true }
  ]);
  return (
    <div className="full-h flex-col">
      <SharedSpace>
        <div className={"left_part " + (tab === "Details" ? "open" : "close")}>
          <div className="full-h custom-sb">
            <StatsTable stats={artSBnes} breakdown={false} />
          </div>
        </div>
        <div
          className={"right_part " + (tab === "Set Bonus" ? "open" : "close")}
        >
          <div className="full-h hide-sb">
            <SetBonus sets={sets} />
          </div>
        </div>
      </SharedSpace>
      <div className="mt-3">{switcher}</div>
    </div>
  );
}

function Constellation() {
  const { name, constellation } = useSelector(selectChar);
  const dispatch = useDispatch();
  return (
    <ConsList
      char={{ name, constellation }}
      handleClick={(i) =>
        dispatch(TOGGLE_CONS(constellation === i + 1 ? i : i + 1))
      }
    />
  );
}

function Talents() {
  const char = useSelector(selectChar);
  const party = useSelector(selectParty);
  const dispatch = useDispatch();
  return (
    <TalentList
      char={char}
      party={party}
      upgradeTl={(tlType, lv) => dispatch(CHANGE_TALENT({ tlType, lv }))}
    />
  );
}
