import { createSelector } from "@reduxjs/toolkit";
import { useMemo, useState } from "react";
import { FaFilter } from "react-icons/fa";
import { useSelector } from "react-redux";
import { selectMyArts } from "../../app/databaseSlice";
import { artNames, artTypes } from "../../configs";
import { findById, initArtStatFilter } from "../../helpers";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box, HiddenSpace } from "../../styledCpns/Layout";
import { ButtonBar, CloseBtn } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import ArtInvFilter from "./ArtInvFilter";
import { EquippedChar, filterArtIdsBySetsAndStats } from "./Common";
import useInvRack from "./hooks/useInvRack";
import { MyArtCard } from "./MyItemCard";
import { DbCard, Inventory, StyledHeading } from "./styles";

const selArtByType = createSelector(
  selectMyArts,
  (_, type) => type,
  (myArts, type) => myArts.filter((p) => p.type === type)
);

export default function ArtInventory({
  artType,
  curPieces,
  owner,
  btnText,
  btnClick,
  close
}) {
  const [filterOn, setFilterOn] = useState(false);
  const [comparing, setComparing] = useState(false);
  const curArt = curPieces[artTypes.indexOf(artType)];

  const data = useSelector((state) => selArtByType(state, artType));
  const [stats, setStats] = useState(initArtStatFilter());
  const [sets, setSets] = useState([]);
  const filteredIds = useMemo(
    () => filterArtIdsBySetsAndStats(data, sets, stats),
    [data, sets, stats]
  );
  const [invRack, chosenID] = useInvRack({
    items: data,
    itemType: "Artifact",
    filteredIds
  });
  const chosenArt = findById(data, chosenID);

  return (
    <Modal standard close={close}>
      <Box height="10%" className="p-2">
        <StyledHeading>
          {window.innerWidth > 380 && (
            <p className="heading_text">{artNames[artType]}</p>
          )}
          <CloseBtn className="close_btn" onClick={close} />
          <IconBtn
            className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
            onClick={() => setFilterOn(!filterOn)}
          >
            <FaFilter />
          </IconBtn>
          <ArtInvFilter
            filterOn={filterOn}
            arts={data}
            artType={artType}
            filter={{ stats, setStats, sets, setSets }}
            close={() => setFilterOn(false)}
          />
        </StyledHeading>
      </Box>
      <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
        <Inventory className="hide-sb">
          {invRack}
          <div className="flex-col justify-between">
            <DbCard className="relative">
              {curArt ? (
                <AbsoluteBox
                  className="full-h"
                  top={0}
                  right="calc(100% - 1rem)"
                  zIndex={1}
                >
                  <HiddenSpace
                    className={"full-h hide-sb" + (comparing ? " show" : "")}
                    openWidth="15.75rem"
                  >
                    <Box
                      className="pl-3 pr-2 py-3 full-h flex-col"
                      width="15.75rem"
                      bgColor="cardBg1"
                      borderRadius="0.5rem 0 0 0.5rem"
                    >
                      <MyArtCard art={curArt} mutable={false} ssSpace={3} />
                      <div className="pt-3 grow-1 flex-center">
                        <Text align="center" color="orange">
                          Current equipment
                        </Text>
                      </div>
                    </Box>
                  </HiddenSpace>
                </AbsoluteBox>
              ) : null}
              <Box className="hide-sb" width="16rem" height="24rem">
                {chosenArt ? (
                  <MyArtCard art={chosenArt} mutable={false} ssSpace={3} />
                ) : null}
              </Box>
              {chosenArt && chosenArt.user !== owner ? (
                <ButtonBar
                  className="mt-4"
                  variants={[comparing ? "neutral" : "default", "positive"]}
                  texts={["Compare", btnText]}
                  availables={[Boolean(curArt), true]}
                  handlers={[
                    () => {
                      if (curArt) setComparing(!comparing);
                    },
                    () => {
                      btnClick(chosenArt);
                      close();
                    }
                  ]}
                />
              ) : null}
            </DbCard>
            {chosenArt?.user ? <EquippedChar user={chosenArt.user} /> : null}
          </div>
        </Inventory>
      </Box>
    </Modal>
  );
}
