import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../../data/characters";
import {
  selectCharData,
  INIT_SESSION_WITH_CHAR
} from "../../../app/calculatorSlice";
import { RESET_CALC_UI } from "../../../app/uiSlice";
import Picker from "../../Picker";
import { Card } from "../styles";
import Content from "./Content";
import { findByName } from "../../../helpers";
import { Button } from "../../../styledCpns/Inputs";

export default function CalcChar() {
  const charData = useSelector(selectCharData);
  const [pickerOn, setPickerOn] = useState(false);
  return (
    <Card className="px-4 py-3 flex-col" bgColor="cardBg1">
      {charData ? (
        <Content setPickerOn={setPickerOn} />
      ) : (
        <Button
          className="mx-auto glow-on-hover"
          variant="positive"
          onClick={() => setPickerOn(true)}
        >
          Choose a Character
        </Button>
      )}
      {pickerOn && <CharPicker close={() => setPickerOn(false)} />}
    </Card>
  );
}

function CharPicker({ close }) {
  const { myChars, myWps, myArts } = useSelector((state) => state.database);
  const dispatch = useDispatch();
  const mixedList = [];
  for (const { name, code, beta, icon, rarity, vision, weapon } of characters) {
    const char = { code, beta, icon, rarity, vision, weapon };
    const existedChar = findByName(myChars, name);
    if (existedChar) {
      mixedList.push({ ...existedChar, ...char });
    } else {
      mixedList.push({ name, ...char });
    }
  }
  return (
    <Picker
      data={mixedList}
      dataType="Character"
      close={close}
      pick={(pickedChar) => {
        dispatch(INIT_SESSION_WITH_CHAR({ pickedChar, myWps, myArts }));
        dispatch(RESET_CALC_UI());
      }}
    />
  );
}
