import { createSelector } from "@reduxjs/toolkit";
import { memo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  REMOVE_ART,
  selectMyArts,
  selectMyChars,
  SWAP_ART_USER
} from "../../../app/databaseSlice";
import { findArtifact, findCharacter } from "../../../data/controllers";
import { findById } from "../../../helpers";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar, Confirmation } from "../../CommonCpns";
import Picker from "../../Picker";
import { EquippedChar, ItemRemoveDialog } from "../Common";
import { MyArtCard } from "../MyItemCard";
import { DbCard } from "../styles";

const selArtById = createSelector(
  selectMyArts,
  (_, ID) => ID,
  (myArts, ID) => findById(myArts, ID)
);

export default memo(RightSection);

function RightSection({ chosenID, filteredIds, setChosenID }) {
  const [pickerOn, setPickerOn] = useState(false);
  const [removing, setRemoving] = useState(false);
  const [newUser, setNewUser] = useState(null);
  const art = useSelector((state) => selArtById(state, chosenID));
  const dispatch = useDispatch();

  const swap = (name) =>
    dispatch(
      SWAP_ART_USER({
        newUser: name,
        artID: art.ID,
        artType: art.type,
        oldUser: art.user
      })
    );

  return (
    <div className="flex-col justify-between">
      <DbCard>
        <Box className="hide-sb" width="18.75rem" height="25rem">
          {art ? <MyArtCard art={art} mutable={true} /> : null}
        </Box>
        {art ? (
          <ButtonBar
            className="mt-3"
            texts={["Remove", "Equip"]}
            handlers={[() => setRemoving(true), () => setPickerOn(true)]}
          />
        ) : null}
      </DbCard>
      {art?.user ? <EquippedChar user={art.user} /> : null}
      {pickerOn && (
        <CharPicker
          user={art.user}
          setNewUser={setNewUser}
          swap={swap}
          close={() => setPickerOn(false)}
        />
      )}
      {removing && (
        <ItemRemoveDialog
          item={art}
          itemType="Artifact"
          filteredIds={filteredIds}
          removeItem={(item) => dispatch(REMOVE_ART(item))}
          setChosenID={setChosenID}
          close={() => setRemoving(false)}
        />
      )}
      {newUser ? (
        <Confirmation
          message={
            <>
              <b>{art.user}</b> is currently using "
              <b>{findArtifact(art).name}</b>". Swap?
            </>
          }
          close={() => setNewUser(null)}
          confirm={() => swap(newUser)}
        />
      ) : null}
    </div>
  );
}

function CharPicker({ user, setNewUser, swap, close }) {
  const myChars = useSelector(selectMyChars);
  const data = [];
  for (const char of myChars) {
    const { name, constellation } = char;
    if (name !== user) {
      const info = findCharacter(char, "code icon rarity vision weapon");
      data.push({ name, constellation, ...info });
    }
  }
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => {
        if (user) {
          setNewUser(name);
        } else {
          swap(name);
        }
      }}
    />
  );
}
