import { useState } from "react";
import { FaCog } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  CHANGE_CUR_SETUP,
  PICK_WP_IN_DB,
  selectArt,
  selectCharData,
  selectCurSetupI,
  selectSetups,
  SET_ART_PIECE
} from "../../../app/calculatorSlice";
import {
  CHANGE_STD_SETUP,
  OPEN_SETTINGS,
  selectCmpSetupIs,
  selectStdSetupI
} from "../../../app/uiSlice";
import { artifactIcons, artTypes, wikiImg } from "../../../configs";
import { btnState, indexByName } from "../../../helpers";
import { useGetHeight } from "../../../helpers/hooks";
import { Img } from "../../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { colors } from "../../../theme";
import ArtInventory from "../../Database/ArtInventory";
import WpInventory from "../../Database/WpInventory";
import PrePicker from "../../Picker/PrePicker";
import Settings, { ImportBtn } from "../Settings";
import { Card, MainSelect } from "../styles";
import SectionArts from "./SectionArts";
import SectionParty from "./SectionParty";
import SectionWp from "./SectionWp";

const StyledIcon = styled(Img)`
  border-radius: 50%;
  &:hover {
    background-color: ${colors.lightGold};
  }
`;

const ChooseStandardButton = () => {
  const cmpIs = useSelector(selectCmpSetupIs);
  const curI = useSelector(selectCurSetupI);
  const chosen = useSelector(selectStdSetupI) === curI;
  const dispatch = useDispatch();
  if (cmpIs.length === 1) {
    return <Box width="5.425rem" />;
  }
  return (
    <Button
      className={btnState(!chosen)}
      variant="positive"
      onClick={() => {
        if (!chosen) dispatch(CHANGE_STD_SETUP(curI));
      }}
    >
      Choose
    </Button>
  );
};

export default function PreCheck() {
  const charData = useSelector(selectCharData);
  if (!charData) {
    return (
      <Card className="p-3" bgColor="cardBg3">
        <ImportBtn />
      </Card>
    );
  }
  return <SetupManager charData={charData} />;
}

function SetupManager({ charData }) {
  const setups = useSelector(selectSetups);
  const curI = useSelector(selectCurSetupI);
  const [inventory, setInv] = useState(null);
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [ref, height] = useGetHeight();
  const dispatch = useDispatch();
  return (
    <Card
      ref={ref}
      className="p-3 flex-col relative overflow-hidden"
      bgColor="cardBg3"
    >
      <MainSelect
        tab={setups[curI].name}
        setTab={(name) => dispatch(CHANGE_CUR_SETUP(indexByName(setups, name)))}
        options={setups.map((st) => st.name)}
      />
      <div id="setupManager_body" className="mt-3 grow-1 hide-sb scroll-smooth">
        <SectionParty />
        <SectionWp />
        <SectionArts />
      </div>
      <div className="mt-3 flex align-center">
        <ChooseStandardButton />
        <IconBtn
          className="mx-auto glow-on-hover"
          fontSize="1.25rem"
          variant="positive"
          onClick={() => dispatch(OPEN_SETTINGS())}
        >
          <FaCog />
        </IconBtn>
        <div className="flex">
          <StyledIcon
            width="2.5rem"
            className="mr-1 pointer"
            src={wikiImg("7/7b/Icon_Inventory_Weapons")}
            alt="weapons"
            draggable={false}
            onClick={() => setInv("weapons")}
          />
          <StyledIcon
            width="2.5rem"
            className="pointer"
            src={wikiImg("6/6a/Icon_Inventory_Artifacts")}
            alt="artifacts"
            draggable={false}
            onClick={() => setPrePickerOn(true)}
          />
        </div>
      </div>
      <Settings height={height} />
      {inventory === "weapons" && (
        <WpInventory
          wpType={charData.weapon}
          close={() => setInv(null)}
          btnText="Pick"
          btnClick={(targetWp) => dispatch(PICK_WP_IN_DB(targetWp))}
        />
      )}
      {prePickerOn && (
        <PrePicker
          choices={artifactIcons}
          pick={(artType) => {
            setInv(artType);
            setPrePickerOn(false);
          }}
          close={() => setPrePickerOn(false)}
        />
      )}
      {![null, "weapons"].includes(inventory) && (
        <ArtPicker artType={inventory} close={() => setInv(null)} />
      )}
    </Card>
  );
}

function ArtPicker({ artType, close }) {
  const { pieces } = useSelector(selectArt);
  const dispatch = useDispatch();
  return (
    <ArtInventory
      curPieces={pieces}
      artType={artType}
      close={close}
      btnText="Pick"
      btnClick={({ user, ...info }) =>
        dispatch(
          SET_ART_PIECE({
            pieceIndex: artTypes.indexOf(artType),
            newPiece: info
          })
        )
      }
    />
  );
}
