import { createSelector } from "@reduxjs/toolkit";
import { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  CHOOSE_SETUP,
  selectChosenSetupID,
  selectMySetups
} from "../../app/setupsSlice";
import { findById, indexById } from "../../helpers";
import { Box } from "../../styledCpns/Layout";
import { NoItems } from "../Database/Common";
import Setup from "./Setup";

const selActualSetup = createSelector(
  selectMySetups,
  (_, id) => id,
  (mySetups, id) => findById(mySetups, id)
);

function ActualSetup({ setup, setUtil, setModalType }) {
  const actualSetup = useSelector((state) =>
    selActualSetup(state, setup.shownID)
  );
  return (
    <Setup
      ID={setup.ID}
      setup={actualSetup}
      stName={setup.name}
      allIDs={setup.allIDs}
      setUtil={setUtil}
      setModalType={setModalType}
    />
  );
}

function SetupClassifier({ setup, setUtil, setModalType }) {
  const chosenID = useSelector(selectChosenSetupID);
  const dispatch = useDispatch();
  const { ID } = setup;
  return (
    <Box
      className="mb-3 px-2 pt-3 pb-2"
      borderRadius="0.5rem"
      bgColor="cardBg3"
      boxShadow={ID === chosenID ? "0 0 5px 3px green" : "var(--cmn-shadow)"}
      onClick={() => dispatch(CHOOSE_SETUP(ID))}
    >
      {setup.type === "original" ? (
        <Setup
          ID={ID}
          setup={setup}
          setUtil={setUtil}
          setModalType={setModalType}
        />
      ) : (
        // setup.type === "complex"
        <ActualSetup
          setup={setup}
          setUtil={setUtil}
          setModalType={setModalType}
        />
      )}
    </Box>
  );
}

const selVisibleSetups = createSelector(selectMySetups, (mySetups) =>
  mySetups.filter((setup) => setup.type !== "combined")
);

export default function SetupList({ setUtil, setModalType }) {
  const chosenID = useSelector(selectChosenSetupID);
  const visibleSetups = useSelector(selVisibleSetups);
  const ref = useRef();

  useEffect(() => {
    if (chosenID) {
      const index = indexById(visibleSetups, chosenID);
      ref.current.scrollTop = 326 * index - (index > 0 ? 40 : 0);
    }
  }, [chosenID]);

  return (
    <Box
      ref={ref}
      className="pl-2 pt-2 flex-col align-start overflow-auto scroll-smooth"
      width="40.5rem"
      minWidth="40.5rem"
    >
      {visibleSetups.length ? (
        visibleSetups.map((setup) => (
          <SetupClassifier
            key={setup.ID}
            setup={setup}
            setUtil={setUtil}
            setModalType={setModalType}
          />
        ))
      ) : (
        <div className="full-w">
          <NoItems type="Setups" />
        </div>
      )}
    </Box>
  );
}
