import { createSelector } from "@reduxjs/toolkit";
import { memo, useState } from "react";
import isEqual from "react-fast-compare";
import { FaCalculator, FaInfo, FaUnlink, FaWrench } from "react-icons/fa";
import { useSelector } from "react-redux";
import { selectChosenSetupID, selectMySetups } from "../../app/setupsSlice";
import calculateAll, { charDataSelect } from "../../calculators/calculateAll";
import { getCharacterData } from "../../data/controllers";
import { deepCopy, findById } from "../../helpers";
import { Green, Red, Text } from "../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import { HowToModal } from "../CommonCpns";
import { Warehouse } from "../Database/styles";
import { AddCtrl, CombineCtrl, RemoveCtrl, ShareCtrl } from "./Controllers";
import DmgResults from "./DmgResults";
import SetupList from "./SetupList";
import SetupModal from "./SetupModal";

const selActualChosenSetup = createSelector(
  selectMySetups,
  selectChosenSetupID,
  (mySetups, chosenID) => {
    const setup = findById(mySetups, chosenID);
    return setup && setup.type === "complex"
      ? findById(mySetups, setup.shownID)
      : setup;
  }
);

const MySetupsMain = memo(({ dmg, setUtil, setModalType }) => {
  const chosenSetup = useSelector(selActualChosenSetup);
  return (
    <Warehouse>
      <Box className="btn_bar" height="2.5rem">
        <IconBtn
          className="mr-3 glow-on-hover"
          variant="positive"
          size="1.625rem"
          fontSize="0.875rem"
          onClick={() => setUtil({ type: "intro" })}
        >
          <FaInfo />
        </IconBtn>
        <Button
          className="glow-on-hover"
          variant="positive"
          onClick={() => setUtil({ type: "combine" })}
        >
          Combine
        </Button>
      </Box>
      <Box className="body" height="35rem">
        <SetupList setModalType={setModalType} setUtil={setUtil} />
        <DmgResults setup={chosenSetup} dmg={dmg} />
      </Box>
    </Warehouse>
  );
}, isEqual);

export default function MySetups() {
  const [modalType, setModalType] = useState(null);
  const [util, setUtil] = useState({ type: "", ID: null });
  const chosenSetup = useSelector(selActualChosenSetup);

  const closeUtil = () => setUtil({ type: "", ID: null });

  let charData;
  let infusion;
  let ATTRs;
  let artSBnes;
  let rxnBnes;
  let dmg;

  if (chosenSetup) {
    const {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      customMCs = { BCs: [], DCs: [] },
      party,
      target
    } = chosenSetup;
    charData = getCharacterData(char, charDataSelect);
    [infusion, ATTRs, , rxnBnes, artSBnes, dmg] = calculateAll(
      deepCopy(char),
      charData,
      selfMCs,
      party,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      customMCs,
      target
    );
  }
  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <MySetupsMain dmg={dmg} setUtil={setUtil} setModalType={setModalType} />
      {modalType && chosenSetup && (
        <SetupModal
          type={modalType}
          result={{ charData, infusion, ATTRs, artSBnes, rxnBnes }}
          setup={chosenSetup}
          close={() => setModalType(null)}
        />
      )}
      {util.type === "remove" && util.ID && (
        <RemoveCtrl removedID={util.ID} close={closeUtil} />
      )}
      {util.type === "share" && util.ID && (
        <ShareCtrl setup={chosenSetup} close={closeUtil} />
      )}
      {util.type === "add" && util.ID && (
        <AddCtrl addingID={util.ID} close={closeUtil} />
      )}
      {util.type === "combine" && <CombineCtrl close={closeUtil} />}
      {util.type === "intro" && <Info close={closeUtil} />}
    </Box>
  );
}

function Info({ close }) {
  return (
    <HowToModal
      content={
        <div>
          <Text lineHeight={1.7}>
            - <Green>Modify Setups</Green>: When you press the <FaWrench />{" "}
            wrench icon on a Setup, you're pushing a <Red>copy</Red> of it to
            the Calculator, so don't forget to save the modified copy if you
            want to apply the changes to that Setup.
          </Text>
          <Text className="mt-2" lineHeight={1.7}>
            - <Green>Complex Setup</Green>: It is the result of combining Setups
            of 4 party members. On this complex, switch to teammates' Setups by
            pressing their icons. Break the complex into individual Setups again
            by pressing the <FaUnlink /> link / chain icon before its name.
          </Text>
          <Text className="mt-2" lineHeight={1.7}>
            - You cannot change teammates when modifying the copy of a Setup
            that is in a complex. However you can make a copy of that copy in
            the Calculator and work on it.
          </Text>
          <Text className="mt-2" lineHeight={1.7}>
            - You can build <Green>teammate's Setups</Green> based on a saved
            Setup by hovering over teammate's icon and press the{" "}
            <FaCalculator /> calculator icon that would appear. Party members
            and Target will be the same. Some Modifiers will remain activated.
          </Text>
        </div>
      }
      close={close}
    />
  );
}
