import { createSelector } from "@reduxjs/toolkit";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ADD_ART, selectMyArts } from "../../../app/databaseSlice";
import { artifactIcons } from "../../../configs";
import { initArtStatFilter } from "../../../helpers";
import { Box } from "../../../styledCpns/Layout";
import { ArtPicker } from "../../Picker/ItemPicker";
import PrePicker from "../../Picker/PrePicker";
import { filterArtIdsBySetsAndStats } from "../Common";
import useInvRack from "../hooks/useInvRack";
import useTypeFilter from "../hooks/useTypeFilter";
import { Warehouse } from "../styles";
import BtnBar from "./BtnBar";
import RightSection from "./RightSection";

const selFilteredIds = createSelector(
  selectMyArts,
  (_, types) => types,
  (_, __, sets) => sets,
  (_, __, ___, stats) => stats,
  (myArts, types, sets, stats) => {
    let result = types.length
      ? myArts.filter((p) => types.includes(p.type))
      : myArts;
    return filterArtIdsBySetsAndStats(result, sets, stats);
  }
);

export default function MyArtifacts() {
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [artType, setArtType] = useState(null);
  const [typeFilter, types, setTypes] = useTypeFilter(false);
  const [sets, setSets] = useState([]);
  const [stats, setStats] = useState(initArtStatFilter());
  const filteredIds = useSelector((state) =>
    selFilteredIds(state, types, sets, stats)
  );
  const [invRack, chosenID, setChosenID] = useInvRack({
    items: useSelector(selectMyArts),
    itemType: "Artifact",
    filteredIds
  });
  const dispatch = useDispatch();

  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <Warehouse>
        <BtnBar
          filter={{ types, setTypes, sets, setSets, stats, setStats }}
          typeFilter={typeFilter}
          setPrePickerOn={setPrePickerOn}
        />
        <div className="body">
          {invRack}
          <RightSection
            chosenID={chosenID}
            filteredIds={filteredIds}
            setChosenID={setChosenID}
          />
        </div>
      </Warehouse>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={artifactIcons}
          pick={(artType) => {
            setArtType(artType);
            setPrePickerOn(false);
          }}
        />
      )}
      {artType && (
        <ArtPicker
          needMassAdd={true}
          artType={artType}
          close={() => setArtType(null)}
          pick={(newItem) => {
            const ID = Date.now();
            dispatch(ADD_ART({ ID, user: null, ...newItem }));
            setChosenID(ID);
          }}
        />
      )}
    </Box>
  );
}
