import { useRef, useState } from "react";
import encodeExportedData from "../../helpers/encode";
import { Text } from "../../styledCpns/DataDisplay";
import { TextArea } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar } from "./index";
import Modal from "./Modal";

export default function ShareDialog({ data, setupName, close }) {
  const [status, setStatus] = useState(0);
  const inputRef = useRef();
  return (
    <Modal close={close}>
      <Box
        className="px-4 pt-3 pb-4 flex-col white-glow-shadow"
        width="21.75rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text
          className="mb-2 px-2"
          variant="h5"
          color="orange"
          bold
          align="center"
        >
          Share "{setupName}"
        </Text>
        <TextArea
          ref={inputRef}
          className="p-2"
          rows="15"
          value={encodeExportedData(data)}
          readOnly
        />
        {status > 0 && (
          <Text
            className="mt-2"
            align="center"
            color={status === 1 ? "green" : "lightRed"}
          >
            {status === 1
              ? "Successfully copied to Clipboard."
              : "We're sorry. Your browser does not allow/support this function."}
          </Text>
        )}
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Copy"]}
          handlers={[
            close,
            () => {
              navigator.clipboard.writeText(inputRef.current.value).then(
                () => setStatus(1),
                () => setStatus(2)
              );
            }
          ]}
        />
      </Box>
    </Modal>
  );
}
