import { useState } from "react";
import { FaPlus } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import {
  ADD_TEAMMATE,
  COPY_PARTY,
  REMOVE_TEAMMATE,
  selectCharData,
  selectCurSetupI,
  selectSetups
} from "../../../app/calculatorSlice";
import characters from "../../../data/characters";
import { indexByName } from "../../../helpers";
import { IconBtn } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { TmFilledSlot } from "../../CommonCpns";
import Picker from "../../Picker";
import { CopySection, Pedestal } from "../styles";

export default function SectionParty() {
  const charData = useSelector(selectCharData);
  const parties = useSelector((state) => state.calculator.parties);
  const setups = useSelector(selectSetups);
  const curI = useSelector(selectCurSetupI);
  const dispatch = useDispatch();
  const party = parties[curI];
  const [pickerTmI, setPickerTmI] = useState(null);

  const copyOpts = [];
  if (party.every((tm) => !tm)) {
    for (let i in parties) {
      if (parties[i].some((tm) => tm)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  const isOg = setups[curI].type === "original";
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection
          options={copyOpts}
          copy={(name) => dispatch(COPY_PARTY(indexByName(setups, name)))}
        />
      ) : null}
      <Box className="px-2 flex justify-around" width="100%">
        {party.map((tm, tmIndex) => (
          <Box
            key={tmIndex}
            className="relative"
            width="4.5rem"
            height="4.5rem"
          >
            {tm ? (
              <TmFilledSlot
                mutable={isOg}
                name={tm.name}
                handleClick={() => {
                  if (isOg) setPickerTmI(tmIndex);
                }}
                removeTeammate={() => dispatch(REMOVE_TEAMMATE(tmIndex))}
              />
            ) : (
              <IconBtn
                color="rgb(255, 255, 255, 0.7)"
                bgColor="cardBg3"
                size="100%"
                fontSize="1.5rem"
                className="glow-on-hover"
                onClick={() => setPickerTmI(tmIndex)}
              >
                <FaPlus />
              </IconBtn>
            )}
          </Box>
        ))}
      </Box>
      {pickerTmI !== null && (
        <CharPicker
          charName={charData.name}
          party={party}
          tmIndex={pickerTmI}
          close={() => setPickerTmI(null)}
        />
      )}
    </Pedestal>
  );
}

function CharPicker({ charName, party, tmIndex, close }) {
  const dispatch = useDispatch();
  const remains = [];
  for (const { code, beta, name, icon, rarity, vision, weapon } of characters) {
    if (name !== charName && !party.find((tm) => tm?.name === name)) {
      remains.push({ code, beta, name, icon, rarity, vision, weapon });
    }
  }
  return (
    <Picker
      data={remains}
      dataType="Character"
      close={close}
      pick={({ name, vision, weapon }) =>
        dispatch(ADD_TEAMMATE({ name, vision, weapon, tmIndex }))
      }
    />
  );
}
