import styled from "styled-components";
import { colors } from "../../theme";

export const DbCard = styled.div`
  width: ${(props) => props.width};
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: ${colors.cardBg1};
`;

export const StyledHeading = styled.div`
  position: relative;
  height: 100%;
  padding-left: 1rem;
  display: flex;
  align-items: center;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: ${colors.orange};
  .heading_text {
    width: 100%;
    font-size: 1.375rem;
    font-weight: 700;
    line-height: 1.235;
    color: ${colors.black};
    text-align: center;
  }
  .close_btn {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background-color: ${colors.black};
  }
  .filter_btn {
    position: absolute;
    top: 50%;
    left: 20px;
    width: 1.75rem;
    height: 1.75rem;
    min-height: 1.75rem;
    margin-top: -0.825rem;
    font-size: 0.825rem;
    background-color: ${colors.black};
    color: ${colors.white};
    &.filterOn {
      color: ${colors.green};
    }
  }
`;

const cellCss = `
  position: relative;
  flex-grow: 0;
  flex-basis: 33.33%;
  max-width: 33.33%;
  padding: 0.5rem;
  @media (min-width: 769px) {
    max-width: 25%;
    flex-basis: 25%;
  }
`;

export const Warehouse = styled.div`
  position: relative;
  width: calc(100% - 3rem);
  height: 90%;
  margin-top: 2rem;
  .btn_bar {
    position: absolute;
    bottom: calc(100% + 1rem);
    left: 0;
    padding-left: 0.5rem;
    display: flex;
    align-items: center;
  }
  .body {
    height: 100%;
    padding-bottom: 1.5rem;
    display: flex;
    overflow: auto;
  }
  .rack {
    width: 43rem;
    min-width: 20.75rem;
    height: 100%;
    padding-right: 1rem;
    .cell {
      ${cellCss}
      @media (min-width: 896px) {
        max-width: 20%;
        flex-basis: 20%;
      }
      @media (min-width: 992px) {
        max-width: 16.66%;
        flex-basis: 16.66%;
      }
    }
  }
  @media (min-width: 1050px) {
    width: 1020px;
  }
`;

export const Inventory = styled.div`
  height: 100%;
  display: flex;
  .rack {
    height: 100%;
    flex-grow: 1;
    padding-right: 0.5rem;
    overflow: auto;
    .cell {
      ${cellCss}
      @media (min-width: 1025px) {
        max-width: 16.66%;
        flex-basis: 16.66%;
      }
    }
  }
`;
