import cn from "classnames";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  MODIFY_TARGET,
  selectCharData,
  selectInfusion,
  selectTarget
} from "../../../../app/calculatorSlice";
import { lib } from "../../../../library";
import {
  CollapseHeading,
  Link,
  Span,
  Text
} from "../../../../styledCpns/DataDisplay";
import { colors } from "../../../../theme";
import { Collapse } from "../../../CommonCpns/HiddenSpace";
import Monster from "./Monster";

export default function Target() {
  const target = useSelector(selectTarget);
  const infusion = useSelector(selectInfusion);
  const charData = useSelector(selectCharData);
  const dispatch = useDispatch();
  const [active, setActive] = useState(false);
  const resTypes = Object.keys(target).filter((field) => field !== "Level");

  return (
    <div>
      <div className="mt-2 px-3">
        <Line
          label="Level"
          color="lightGold"
          value={target.Level}
          handleChange={(e) => {
            const lv = +e.target.value;
            if (!isNaN(lv) && lv >= 0 && lv <= 100) {
              dispatch(MODIFY_TARGET({ type: "Level", value: Math.round(lv) }));
            }
          }}
        />
        <Monster />
      </div>

      <CollapseHeading
        className={cn({ active })}
        onClick={() => setActive(!active)}
      >
        Resistances
      </CollapseHeading>
      <Collapse in={active}>
        <div className="pt-3 px-3">
          {resTypes.map((type, i) => {
            const elmt = type.split(" ")[0];
            return (
              <Line
                key={i}
                label={type}
                color={elmt === "Physical" ? "White" : elmt}
                value={target[type]}
                handleChange={(e) => {
                  const res = +e.target.value;
                  if (!isNaN(res) && res <= 999) {
                    dispatch(MODIFY_TARGET({ type, value: Math.round(res) }));
                  }
                }}
              />
            );
          })}
          <Text>
            You can search for your target's Resistances on{" "}
            <Link
              href="https://genshin-impact.fandom.com/wiki/Resistance#Base_Enemy_Resistances"
              rel="noreferrer"
              target="_blank"
            >
              this page of the Genshin Impact Wiki
            </Link>
            .
          </Text>
        </div>
      </Collapse>
      <InfusionNotes infusion={infusion} charData={charData} />
    </div>
  );
}

const Input = styled.input`
  padding: 0.5rem 0.5rem 0.25rem;
  width: 5rem;
  border: none;
  border-radius: 0.25rem;
  text-align: right;
  font-size: 1.125rem;
  &:focus {
    box-shadow: 0 0 0 4px ${colors.red};
  }
`;

function Line(props) {
  return (
    <div className="mb-3 flex justify-between align-center">
      <Text variant="h6" color={props.color}>
        {props.label}
      </Text>
      <Input type="text" value={props.value} onChange={props.handleChange} />
    </div>
  );
}

export function InfusionNotes({ infusion, charData: { vision, weapon } }) {
  const notes =
    weapon === "Catalyst"
      ? [
          ["NA", vision],
          ["CA", vision],
          ["PA", vision]
        ]
      : Object.entries(infusion);
  if (weapon === "Bow") {
    notes[1][0] = "AS";
    notes.splice(2, 0, ["CAS", vision]);
  }
  return (
    <div className="mt-2 pr-2">
      <Text variant="h6" color="lightGold">
        Notes:
      </Text>
      {notes.map(([attPatt, elmt], i) => (
        <Text key={i} className="mt-1">
          <b>{lib[attPatt]}</b> deal <Span color={elmt}>{elmt} DMG</Span>.
        </Text>
      ))}
    </div>
  );
}
