import { memo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  REMOVE_WP,
  selectMyChars,
  selWpById,
  SWAP_WP_USER
} from "../../../app/databaseSlice";
import { findCharacter } from "../../../data/controllers";
import { useGetHeight } from "../../../helpers/hooks";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar } from "../../CommonCpns";
import Picker from "../../Picker";
import { EquippedChar, ItemRemoveDialog } from "../Common";
import { MyWpCard } from "../MyItemCard";
import { DbCard } from "../styles";

export default memo(RightSection);

function RightSection({ chosenID, filteredIds, setChosenID }) {
  const [pickerOn, setPickerOn] = useState(null);
  const [removing, setRemoving] = useState(false);
  const [ref, height] = useGetHeight();
  const weapon = useSelector((state) => selWpById(state, chosenID));
  const dispatch = useDispatch();

  return (
    <div ref={ref} className="full-h flex-col">
      <div className="grow-1 flex align-start">
        <DbCard
          className="flex-col"
          style={{ minHeight: "27rem", maxHeight: height / 16 - 3 + "rem" }}
        >
          <Box className="grow-1 hide-sb" width="18.75rem">
            {weapon ? <MyWpCard weapon={weapon} mutable={true} /> : null}
          </Box>
          {weapon ? (
            <ButtonBar
              className="mt-3"
              texts={["Remove", "Equip"]}
              handlers={[() => setRemoving(true), () => setPickerOn(true)]}
            />
          ) : null}
        </DbCard>
      </div>
      {weapon?.user ? <EquippedChar user={weapon.user} /> : null}
      {pickerOn && (
        <CharPicker weapon={weapon} close={() => setPickerOn(false)} />
      )}
      {removing && (
        <ItemRemoveDialog
          item={weapon}
          itemType="Weapon"
          filteredIds={filteredIds}
          removeItem={(item) => dispatch(REMOVE_WP(item))}
          setChosenID={setChosenID}
          close={() => setRemoving(false)}
        />
      )}
    </div>
  );
}

function CharPicker({ weapon: { ID, user, type }, close }) {
  const data = [];
  for (const char of useSelector(selectMyChars)) {
    const select = "name code icon rarity vision weapon";
    const info = findCharacter(char, select);
    if (info.weapon === type && info.name !== user) {
      data.push({ ...info, constellation: char.constellation });
    }
  }
  const dispatch = useDispatch();
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) =>
        dispatch(SWAP_WP_USER({ newUser: name, wpID: ID, oldUser: user }))
      }
    />
  );
}
