import { useState } from "react";
import { initArtStatFilter } from "../../helpers";
import { AbsoluteBox } from "../../styledCpns/Layout";
import { colors } from "../../theme";
import { ButtonBar } from "../CommonCpns";
import { HiddenScroll } from "../CommonCpns/HiddenSpace";
import useArtSetFilter from "./hooks/useArtSetFilter";
import useArtStatsFilter from "./hooks/useArtStatsFilter";

export function hasDupStat(stats) {
  const existed = [stats.main];
  for (let ss of stats.subs) {
    if (ss === "All") continue;
    if (existed.includes(ss)) return true;
    else existed.push(ss);
  }
  return false;
}

export default function ArtInvFilter({ filterOn, ...rest }) {
  return (
    <AbsoluteBox top="100%" left={0} zIndex={3} className="full-w">
      <HiddenScroll
        on={filterOn}
        className="flex justify-center"
        style={{
          borderRadius: "0 0 0.5rem 0.5rem",
          boxShadow: "var(--cmn-shadow)",
          backgroundColor: colors.cardBg3
        }}
        openHeight="28.35rem"
        duration={150}
        timingFunc="linear"
      >
        <HiddenFilter {...rest} />
      </HiddenScroll>
    </AbsoluteBox>
  );
}

function HiddenFilter({ arts, filter, artType, close }) {
  const [err, setErr] = useState(false);
  const [statsFilter, stats, setStats] = useArtStatsFilter(
    filter.stats,
    err,
    artType
  );
  const [setFilter, sets] = useArtSetFilter(arts, filter.sets, artType);
  const resetable = stats.main !== "All" || stats.subs.some((s) => s !== "All");

  return (
    <div className="p-3 flex hide-sb">
      <div className="flex-col">
        <ButtonBar
          className="mb-2"
          texts={["Reset Stats"]}
          availables={[resetable]}
          handlers={[
            () => {
              if (resetable) setStats(initArtStatFilter());
            }
          ]}
        />
        {statsFilter}
        <ButtonBar
          className="mt-3 pb-2"
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              if (hasDupStat(stats)) {
                setErr(true);
                return;
              }
              filter.setStats(stats);
              filter.setSets(sets);
              close();
            }
          ]}
        />
      </div>
      {setFilter}
    </div>
  );
}
