import { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../data/characters";
import {
  ADD_CHAR,
  CHOOSE_MY_CHAR,
  selectMyChars
} from "../../app/databaseSlice";
import { Button, Select } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import Info from "./Info";
import TopBar from "./TopBar";
import { createSelector } from "@reduxjs/toolkit";
import { extractData } from "../../helpers";

const selExistedNames = createSelector(selectMyChars, (myChars) =>
  myChars.map(({ name }) => name)
);

export default function MyCharacters() {
  const [pickerOn, setPickerOn] = useState(false);
  const charNames = useSelector(selExistedNames);
  const charListRef = useRef();

  return (
    <Box className="full-h flex-col" bgColor="cardBg3">
      {window.innerWidth <= 700 ? (
        <Box className="py-3 flex" bgColor="cardBg2">
          {charNames.length ? (
            <CharSelect names={charNames} setPickerOn={setPickerOn} />
          ) : (
            <Button
              className="mx-auto"
              variant="positive"
              onClick={() => setPickerOn(true)}
            >
              Add New Characters
            </Button>
          )}
        </Box>
      ) : (
        <TopBar
          charNames={charNames}
          setPickerOn={setPickerOn}
          charListRef={charListRef}
        />
      )}
      <div className="grow-1 flex-center">
        <Box className="full-w flex justify-center" height="98%">
          {!!charNames.length && <Info />}
        </Box>
      </div>
      {pickerOn && (
        <CharPicker
          charListRef={charListRef}
          charNames={charNames}
          close={() => setPickerOn(false)}
        />
      )}
    </Box>
  );
}

function CharSelect({ names, setPickerOn }) {
  const chosen = useSelector((state) => state.database.chosenChar);
  const dispatch = useDispatch();
  return (
    <div className="full-w flex-center relative">
      <Select
        className="px-2 py-1"
        color="black"
        borderRadius="0.25rem"
        bgColor="white"
        fontSize="1.5rem"
        bold
        align="center"
        value={chosen}
        onChange={(e) => dispatch(CHOOSE_MY_CHAR(e.target.value))}
      >
        {names.map((name, i) => (
          <option key={i}>{name}</option>
        ))}
      </Select>
      <Button
        className="ml-4"
        variant="positive"
        onClick={() => setPickerOn(true)}
      >
        Add
      </Button>
    </div>
  );
}

const projection = "name code beta icon rarity vision weapon";

function CharPicker({ charNames, charListRef, close }) {
  const wishlist = [];
  for (const dataChar of characters) {
    if (!charNames.includes(dataChar.name)) {
      wishlist.push(extractData(dataChar, projection));
    }
  }
  const dispatch = useDispatch();
  return (
    <Picker
      needMassAdd={true}
      data={wishlist}
      dataType="Character"
      close={close}
      pick={({ name, weapon }) => {
        dispatch(ADD_CHAR({ name, weapon }));
        if (charListRef.current) charListRef.current.scrollLeft = 0;
      }}
    />
  );
}
