import calculateAll, {
  charDataSelect
} from "../../../calculators/calculateAll";
import { allOfclSTypes, hitBnTypes, bnFields, rdAndIg } from "../../../configs";
import { getCharacterData } from "../../../data/controllers";
import { reactions } from "../../../configs";

export function calculateAndTrack(
  char,
  {
    selfMCs,
    wpRack,
    subWpMCs,
    artChest,
    parties,
    elmtMCs,
    customMCs,
    target,
    curSetupI
  }
) {
  const tracker = initTracker();
  const result = calculateAll(
    char,
    getCharacterData(char, charDataSelect),
    selfMCs[curSetupI],
    parties[curSetupI],
    wpRack[curSetupI],
    subWpMCs[curSetupI],
    artChest[curSetupI],
    elmtMCs[curSetupI],
    customMCs[curSetupI],
    target,
    tracker
  );
  // [infusion, ATTRs, hitBnes, rxnBnes, artSBnes, dmg]
  return [tracker, ...result.slice(0, -2), result[5]];
}

function initTracker() {
  const result = {};
  for (const type of allOfclSTypes) {
    result[type] = [];
  }
  for (const type of hitBnTypes) {
    result[type] = {};
    for (const field of bnFields) {
      result[type][field] = [];
    }
  }
  for (const type of reactions) {
    result[type] = [];
  }
  for (const type of rdAndIg) {
    result[type] = [];
  }
  result.tlBnes = {};
  return result;
}

export const getTotal = (arr) =>
  arr.reduce((result, elmt) => result + elmt.value, 0);
