import { useSelector } from "react-redux";
import { selectSubWpMCs, selectWeapon } from "../../../../app/calculatorSlice";
import { NoContent } from "../CmnCpns";
import WpBuffsMain from "./WpBuffsMain";
import WpBuffsSub from "./WpBuffsSub";

export default function WpBuffs() {
  const subWpBCs = useSelector(selectSubWpMCs).BCs;
  const weapon = useSelector(selectWeapon);
  if (!weapon.BCs.length) {
    let noContent = true;
    for (const wpType in subWpBCs) {
      const bc = subWpBCs[wpType];
      if (bc && bc.length) {
        noContent = false;
        break;
      }
    }
    if (noContent) return <NoContent type="buffs" />;
  }
  return (
    <>
      <WpBuffsMain weapon={weapon} />
      <WpBuffsSub subBCs={subWpBCs} />
    </>
  );
}
