import { artMS } from "../../data/artifacts/config";
import { findArtifact, findWeapon } from "../../data/controllers";
import { Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { Confirmation } from "../CommonCpns";

export function EquippedChar({ user }) {
  return (
    <Box className="mt-3 px-4 py-1 flex" bgColor="#ffe7bb">
      <Text bold color="black">
        Equipped: {user}
      </Text>
    </Box>
  );
}

export function NoItems(props) {
  return (
    <div className="pt-5 flex-center">
      <Text variant="h5" bold color="lightRed">
        No {props.type} to display
      </Text>
    </div>
  );
}

export function filterArtIdsBySetsAndStats(arts, sets, stats) {
  let result = [...arts];
  if (sets.length) {
    result = result.filter((p) => sets.includes(p.code));
  }
  function compareMainStat(a, b) {
    const ms = (p) => artMS[p.type][p.mainSType][p.rarity || 5][p.level];
    return ms(b) - ms(a);
  }
  if (stats.main !== "All") {
    result = result.filter((p) => p.mainSType === stats.main);
    if (stats.subs[0] === "All") {
      result.sort(compareMainStat);
    }
  }
  if (stats.subs[0] !== "All") {
    const requires = stats.subs.filter((s) => s !== "All");
    result = result.filter((p) =>
      requires.every((rq) => p.subS.map((ss) => ss.type).includes(rq))
    );
    const getVal = (p, type) => p.subS.find((s) => s.type === type).val;
    result.sort((a, b) => {
      if (stats.main !== "All") {
        const msResult = compareMainStat(a, b);
        if (msResult) {
          return msResult;
        }
      }
      for (const rq of requires) {
        const ssResult = getVal(b, rq) - getVal(a, rq);
        if (ssResult) return ssResult;
      }
      return false;
    });
  }
  return result.map(({ ID }) => ID);
}

export function ItemRemoveDialog({
  item: { ID, user, code, type },
  itemType,
  filteredIds,
  removeItem,
  setChosenID,
  close
}) {
  const finder = itemType === "Weapon" ? findWeapon : findArtifact;
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{finder({ code, type }).name}</b>
          "?{" "}
          {user ? (
            <>
              It is currently used by <b>{user}</b>.
            </>
          ) : null}
        </>
      }
      close={close}
      confirm={() => {
        removeItem({ ID, user, type });
        const index = filteredIds.indexOf(ID);
        if (index !== -1 && filteredIds.length > 1) {
          const move = index < filteredIds.length - 1 ? 1 : -1;
          setChosenID(filteredIds[index + move]);
        }
      }}
    />
  );
}
